.App {
  text-align: center;
  background-color: #000;
  color: #fff;
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.board {
  display: grid;
  grid-template-columns: repeat(4, 80px);
  grid-template-rows: repeat(4, 80px);
  gap: 5px;
  margin-bottom: 20px;
}

.cell {
  width: 80px;
  height: 80px;
  background-color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #444;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
}

.moveHistory {
  max-width: 300px;
  text-align: left;
}

.moveHistory ul {
  list-style: none;
  padding: 0;
}

.moveHistory li {
  margin-bottom: 5px;
}
